@font-face {
  font-family: "Noto Sans KR";
  font-weight: 300;
  src: url("./fonts/NotoSansKR-Light.otf") format("otf");
}

@font-face {
  font-family: "Noto Sans KR";
  font-weight: 400;
  src: url("./fonts/NotoSansKR-Regular.otf") format("otf");
}
@font-face {
  font-family: "Noto Sans KR";
  font-weight: 500;
  src: url("./fonts/NotoSansKR-Medium.otf") format("otf");
}

@font-face {
  font-family: "Noto Sans KR";
  font-weight: 700;
  src: url("./fonts/NotoSansKR-Bold.otf") format("otf");
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
dfn,
del,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
main,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section {
  display: block;
}
/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}
body {
  line-height: 1;
  font-family: "Noto Sans KR", -apple-system, BlinkMacSystemFont, Segoe UI,
    Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji,
    Segoe UI Symbol;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-touch-callout: none;
  text-size-adjust: none;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
select {
  border-style: none;
  background: transparent;
  appearance: none;
}
b,
strong {
  font-weight: bold;
}

a:any-link {
  text-decoration: none;
}
input {
  margin: 0;
}
